import { Combobox } from "@headlessui/react";
import * as React from "react";
import _ from "lodash";
import { useParams } from "react-router-dom";
import {
    useDeveloperInProgressTaskQuery,
    useGetTaskDetailsQuery,
} from "../../../services/api/SingleTaskPageApi";
import Loader from "../../components/Loader";
import { Placeholder } from "../../../global/Placeholder";
import { useAuth } from "../../../hooks/useAuth";

const AssginedToSelection = ({ selected, onSelect, taskCategory }) => {
    const auth = useAuth();
    const [query, setQuery] = React.useState("");
    const [employeeId, setEmployeeId] = React.useState();
    const params = useParams();
    const { data, isFetching } = useGetTaskDetailsQuery(
        `/${params?.taskId}/json?mode=employees`
    );

    console.log("fetching empoloyees", data);
    const { DevProgressStatus, isLoading } = useDeveloperInProgressTaskQuery(
        employeeId,
        {
            skip: !employeeId,
        }
    );

    let employees = [];
    if (taskCategory && taskCategory.id === 5) {
        employees = _.filter(
            data,
            (d) =>
                Number(d.role_id) === 9 &&
                Number(d.id) !== Number(window.Laravel.user.id)
        );
    } else if (taskCategory && taskCategory.id === 7) {
        employees = _.filter(data, (d) => Number(d.role_id) === 10);
    } else if (auth.roleId === 15) {
        employees = _.filter(data, (d) => Number(d.role_id) === 6);
    } else {
        employees = _.filter(
            data,
            (d) =>
                Number(d.role_id) === 5 ||
                Number(d.role_id) === 14 ||
                Number(d.role_id) === 6
        );
    }
    // console.log("employees", employees);
    const filteredData =
        query === ""
            ? employees
            : employees?.filter((employee) => {
                  return employee?.name
                      .toLowerCase()
                      .includes(query.toLowerCase());
              });

    console.log("employees", employees);

    return (
        <Combobox value={selected} onChange={onSelect}>
            <div className="form-group position-relative my-3">
                <label htmlFor="">Assigned To</label>
                <Combobox.Button className="d-flex align-items-center w-100 sp1-selection-display-button">
                    <Combobox.Input
                        onChange={(e) => setQuery(e.target.value)}
                        placeholder="--"
                        displayValue={(value) => value?.name || ""}
                        className="form-control height-35 f-14 sp1-selection-display w-100"
                    />
                    <div className="__icon">
                        <i className="fa-solid fa-sort"></i>
                    </div>
                </Combobox.Button>

                <Combobox.Options className="sp1-select-options">
                    {isFetching && (
                        <div className="sp1-select-option-nodata">
                            <Loader />
                        </div>
                    )}

                    {filteredData?.length === 0 ? (
                        <div className="sp1-select-option-nodata">
                            Nothing found.
                        </div>
                    ) : (
                        filteredData?.map((employee) => (
                            <Option
                                key={employee.id}
                                employee={employee}
                                DevProgressStatus={DevProgressStatus}
                                isLoading={isLoading}
                                setEmployeeId={setEmployeeId}
                            />
                        ))
                    )}
                </Combobox.Options>
            </div>
        </Combobox>
    );
};

export default AssginedToSelection;

const Option = ({ employee, DevProgressStatus, isLoading, setEmployeeId }) => {
    setEmployeeId(employee?.id);
    if (isLoading) {
        return (
            <div className="sp1-select-option mb-2">
                <div className="d-flex align-items-center px-3">
                    <Placeholder />
                </div>
            </div>
        );
    }

    return (
        <Combobox.Option
            key={employee?.id}
            className={({ active }) =>
                `sp1-select-option ${active ? "active" : ""}`
            }
            value={{
                ...employee,
                isOverloaded: DevProgressStatus?.status === 400,
            }}
        >
            {({ selected }) => (
                <div
                    className="d-flex align-items-center"
                    style={{ gap: "10px" }}
                >
                    <div
                        className="rounded-circle"
                        style={{
                            width: "28px",
                            height: "28px",
                            overflow: "hidden",
                        }}
                    >
                        <img
                            src={employee?.image_url}
                            alt={employee?.name}
                            width={100}
                            height={100}
                            className="rounded-circle"
                            style={{
                                width: "100%",
                                height: "100%",
                                objectFit: "fill",
                            }}
                        />
                    </div>
                    <span
                        className={`block truncate ${
                            selected ? "font-medium" : "font-normal"
                        }`}
                    >
                        <span className="mr-2">{employee?.name}</span>
                        {DevProgressStatus?.status === 400 ? (
                            <span className="badge badge-danger">
                                Overloaded
                            </span>
                        ) : employee?.developer_status === 1 ? (
                            <span className="badge badge-warning">
                                Working...
                            </span>
                        ) : (
                            <span className="badge badge-primary">
                                Open to Work
                            </span>
                        )}
                    </span>
                    {selected ? (
                        <span className="ml-auto">
                            <i className="fa-solid fa-check"></i>
                        </span>
                    ) : null}
                </div>
            )}
        </Combobox.Option>
    );
};
